<template>
  <ul class="nav-bar" @click="handleItemclick">
    <li :class="{'item-on':items.addnew}"  mkey='addnew'>抄表</li>
    <li :class="{'item-on':items.history}"  mkey='history'>历史记录</li>
  </ul>
</template>

<script>
export default {
  name: 'Mnavbar',
  data () {
    return {
      items: {
        addnew: true,
        history: false,
      },
      active: 'addnew',
    };
  },
  methods: {
    handleItemclick (e) {
      (this.active != '')&&(this.items[this.active] = false);
      this.items[e.target.getAttribute('mkey')] = true;
      this.active = e.target.getAttribute('mkey');
      this.$emit('select',this.active);
    }
  }
}
</script>

<style scoped>
.nav-bar{
  list-style: none;
  margin-bottom: 10px;
}
.nav-bar li {
  float: left;
  width: 50%;
  text-align: center;
  padding: 15px 0px;
  color: #9a9a9a;
  border-bottom: 2px solid #ffffff;
}
.item-on{
  color: #4a5a6a !important;
  border-bottom: 2px solid #049efe !important;
}
.nav-bar::after{
  content: '';
  display: block;
  clear: both;
}
</style>
