<template>
  <div class="add-from-wrap">
    <el-form label-width="70px" :model="formdata" label-position="left">
      <el-form-item label="名称">
        <el-input v-model="formdata.name"></el-input>
      </el-form-item>
      <el-form-item label="单价">
        <el-input v-model="formdata.price"></el-input>
      </el-form-item>
    </el-form>
    <el-button type="primary" round @click="handleAdd">保存</el-button>
  </div>
</template>

<script>
export default {
  name: 'Addfee',
  data () {
    return {
      formdata: {
        name: '',
        price: '',
      },
      table: {
        water: 'waterfees',
        eletric: 'eletricfees',
      },
      path: '',
      pagetitle: '新增收费项目',
    };
  },
  beforeRouteEnter (to, form, next) {
    next((vm) => {
      vm.$store.commit('settitle', vm.pagetitle);
    });
  },
  beforeRouteEnter (to, from, next) {
    //console.log(this.table[from.path.match(/\/(.+)/)[1]]);
    next( (vm) => {
      vm.path = from.path;
      vm.table = vm.table[vm.path.match(/\/(.+)/)[1]];
      console.log(vm.table);
    });
  },
  methods: {
    handleAdd () {
      const newitem = window.firebase.database().ref(this.table).push();
      newitem.set(this.formdata).then(() => {
        this.$router.push({
          path: this.path,
        });
      }).catch(() => {
        console.log('write failed');
      });
    },
  }
}
</script>

<style scoped>
.add-from-wrap {
  padding: 50px 15px;
  text-align: center;
}
</style>
