<template>
  <div>
    <el-table
      :data="tableData"
      stripe
      style="width: 100%" @row-click="handleRowclick">
      <el-table-column
        prop="number"
        label="房号"
        width="80">
      </el-table-column>
      <el-table-column
        prop="address"
        label="地址"
        :filters="[{ text: '横潭大街32号', value: '横潭大街32号' }, { text: '朝阳巷5号', value: '朝阳巷5号' }]"
        :filter-method="filterAddress"
        width="min-width">
      </el-table-column>
      <el-table-column
        prop="date"
        label="起租日"
        width="100">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import Utils from '../utils.js';
export default {
  name: 'Checkout',
  data () {
    return {
      tableData: [],
      pagetitle: '抄表及打印',
    };
  },
  beforeRouteEnter (to, form, next) {
    next((vm) => {
      vm.$store.commit('settitle', vm.pagetitle);
    });
  },
  created () {
    const that = this;
    window.firebase.database().ref('houses').once('value', (dataSnapshot) => {
      that.tableData = Utils.Object2Array(dataSnapshot.val());
    });
  },
  methods: {
    handleRowclick (row) {
      this.$router.push({
        path: '/generatebill/' + row.id,
      });
    },
    filterAddress (a, b, c) {

      return b.address === a;
    },
  },
}
</script>

